export const setMobileCSS = function () {
	console.log("avseoul->setting mobile_css");

	const work = document.getElementById("works_title") as HTMLElement;
	work.style.width = "100vw";

	const containers = document.getElementsByClassName("ui_container") as any;
	for (const container of containers) {
		container.style["position"] = "auto";
		container.style["height"] = "auto";
		container.style["margin-top"] = "30px";
	}

	const uiThumbMasks = document.getElementsByClassName("ui_thumbnail_mask") as any;
	for (const mask of uiThumbMasks) {
		mask.style["position"] = "relative";
		mask.style["left"] = " ";
	}

	const uiThumbs = document.getElementsByClassName("ui_thumbnail") as any;
	for (const thumb of uiThumbs) {
		thumb.style["width"] = "100vw";
		thumb.style["height"] = "auto";
	}

	const uiDescriptionMasks = document.getElementsByClassName("ui_description_mask") as any;
	for (const mask of uiDescriptionMasks) {
		mask.style['position'] = "relative";
		mask.style['left'] = "0px";
		mask.style['width'] = "100vw";
		mask.style['height'] = "auto";
	}
}

