export const insertNext = function (targetNode: HTMLElement, referenceNode: HTMLElement) {
    referenceNode?.parentNode?.insertBefore(targetNode, referenceNode.nextSibling);
};

export const setLocation = function (id: string) {
    window.location.hash = id;
};

export const getImageAssetURL = function (imageAssets: any[], imageSrc: string) {
	const imageAssetsKey = imageSrc.split(".")[0] as any;
    return Object.entries(imageAssets[imageAssetsKey])[0][1];
}

export const getRandomColorCSS = function() {
	return 'rgb(' + Math.floor(Math.random() * 255) + ',' + Math.floor(Math.random() * 255) + ',' + Math.floor(Math.random() * 255) + ')';
}