import contentJSON from '/json/content.json';
import {
    setMobileCSS, 
    isMobile, 
    insertNext, 
    setLocation, 
    getImageAssetURL,
    getRandomColorCSS
} from '/js/utils';

import {CONTET_JAR, CONTENT_TYPE} from '/js/const';

import imageAssets from './../assets/img/*.*';

const shuffleRandomScreeningContent = function () {
    const seed = Math.floor(Math.random() * CONTET_JAR.length);
    const m = CONTET_JAR[seed];

    const targetDiv = document.getElementById("random_selected_works") as HTMLElement;
    
    const w = parseFloat(getComputedStyle(targetDiv.parentNode as HTMLElement, null).getPropertyValue('width'));
    const h = w * .6666;
    
    targetDiv.style['width'] = w + 'px';
    targetDiv.style['height'] = h + 'px';
    targetDiv.style['background'] = '#000';
    
    targetDiv.innerHTML =
        '<iframe id="mIframe_content" src=\"https://player.vimeo.com/video/'
        + m.url + '\" width=\"' + w + '\" height=\"' + h
        + '\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>';
}

const locateToHash = function () {
    let hash = location.hash;
    hash = hash.split('#')[1];
    const i = document.getElementById(hash)?.firstElementChild?.firstElementChild as HTMLElement;
    i?.click();
}

const linksAlive = function () {
    const links = [].slice.call(document.querySelectorAll("a")) as any[];

    for (const link of links) {
        if (link.className != "is_fucked") {
            link.style["color"] = getRandomColorCSS();
            link.style.backgroundColor = getRandomColorCSS();
        }
    }
};

const renderRainbow = function () {
    const paragraphs = [].slice.call(document.querySelectorAll("p")) as any[];
    const titles = [].slice.call(document.querySelectorAll("av_title")) as any[];
    const links = [].slice.call(document.querySelectorAll("a")) as any[];

    for (const p of paragraphs) {
        p.style.backgroundColor = getRandomColorCSS();

        const words = p.textContent.split(" ");
        p.textContent = '';
        for (const w of words) {
            const tag = document.createElement("rainbow");
            tag.style.color = getRandomColorCSS();

            tag.style.backgroundColor = getRandomColorCSS();

            tag.textContent = w + ' ';
            p.appendChild(tag);
        }
    }

    for (const t of titles) {
       const words = t.textContent.split(" ");
        t.textContent = '';
        for (const w of words) {
            const tag = document.createElement("rainbow");
            tag.style.color = getRandomColorCSS();
            tag.style.backgroundColor = getRandomColorCSS();
            tag.textContent = w + ' ';

            t.appendChild(tag);
        }
    }

    for (const l of links) {
        l.className = "is_fucked";
        l.style.backgroundColor = getRandomColorCSS();
    }
}

const expandContent = function (index: number) {
    const contentContainerClassName = isMobile() ? "content_container_mobile" : "content_container";
    
    // Reset the dom
    const containers = document.getElementsByClassName(contentContainerClassName);
    for (const container of containers)
        container?.parentNode?.removeChild(container);
    
    // Set locatioin
    setLocation(contentJSON.projects[index].id);

    // Construct doms 
    const project = contentJSON.projects[index];
    const targetDom = document.getElementById(project.id);
    const contentContainer = document.createElement('div');
    contentContainer.className = contentContainerClassName;
    
    // Append to target
    insertNext(contentContainer, targetDom);

    // Contruct content doms
    let content: any = null;
    const targetDomWidth = parseFloat(getComputedStyle(targetDom as Element, null).getPropertyValue('width'));
    const targetDomHeight = targetDomWidth * project.content.size.h / project.content.size.w;
    
    switch (project.content.is) {
        case 'video':
            content = buildContentNode(targetDomWidth, targetDomHeight, project.content.url, CONTENT_TYPE.VIDEO);
            break;

        case 'image':
            content = buildContentNode(targetDomWidth, targetDomHeight, project.content.url, CONTENT_TYPE.IMAGE);
            break;

        case 'realtime':
            content = buildContentNode(targetDomWidth, targetDomHeight, project.content.url, CONTENT_TYPE.REALTIME);
            break;
            
        default:
            break;
    }
    if(content !== null) contentContainer.appendChild(content);

    // Content info
    let contentTitle: string = '';
    for (let i = 0; i < 3; i++) {
        // Title
        switch (i) {
            case 0: contentTitle = '> Info'; break;
            case 1: contentTitle = '> Process'; break;
            case 2: contentTitle = '> Credit'; break;
            default: break;
        }

        const title = document.createElement('div');
        title.className = "content_title";
        title.innerHTML = contentTitle;
        
        contentContainer.appendChild(title);

        // Description
        const description = document.createElement('div');
        let infoDescription: any = null;
        let processDescription: any = null;
        switch (i) {
            case 0:
                for (const info of project.info) {
                    switch (info.is) {
                        case 'video':
                            const height = targetDomWidth * info.size.h / info.size.w;
                            infoDescription = buildDescriptionNode(targetDomWidth, height, info, CONTENT_TYPE.VIDEO);

                            break;
                        case 'image':
                            infoDescription = buildDescriptionNode(targetDomWidth, 0, info, CONTENT_TYPE.IMAGE);
                            break;

                        case 'text':
                            infoDescription = buildDescriptionNode(targetDomWidth, 0, info, CONTENT_TYPE.TEXT);
                            break;

                        default:
                            break;
                    }
                    if(infoDescription !== null) description.appendChild(infoDescription);
                }
                break;

            case 1:
                for (const process of project.process) {
                    switch (process.is) {
                        case 'video':
                            const height = targetDomWidth * process.size.h / process.size.w;
                            processDescription = buildDescriptionNode(targetDomWidth, height, process, CONTENT_TYPE.VIDEO);
                            break;

                        case 'image':
                            processDescription = buildDescriptionNode(targetDomWidth, 0, process, CONTENT_TYPE.IMAGE);
                            break;

                        case 'text':
                            processDescription = buildDescriptionNode(targetDomWidth, 0, process, CONTENT_TYPE.TEXT);
                            break;

                        default:
                            break;
                    }
                    if(processDescription !== null) description.appendChild(processDescription);
                }
                break;
            case 2:
                for (const credit of project.credit) {
                    const creditNode = buildCreditNode(credit);

                    if(creditNode !== null) description.appendChild(creditNode);
                }
                break;
            default:
                break;
        }

        if(description !== null) contentContainer.appendChild(description);
    }

    // Github info
    if (project.github !== '') {
        const githubInfo = buildGithubInfoNode(project.github);

        contentContainer.appendChild(githubInfo.title);
        contentContainer.appendChild(githubInfo.body);
    }
};

const buildGithubInfoNode = function(info: string) {
    const title = document.createElement('div') as HTMLElement;
    title.className = 'content_title';
    title.innerHTML = '> Source';

    const body = document.createElement('div') as HTMLElement;
    body.innerHTML = '<p><a href=' + info + ' target=\'_blank\'>github</a></p>';

    return {title: title, body: body};
}

const buildCreditNode = function(info: any) {
    const paragraph = document.createElement('p') as HTMLElement;
    paragraph.innerHTML = '<credit>' + info.role + '</credit>: ' + info.credit;

    return paragraph;
}

const buildDescriptionNode = function(width: number, height: number, info: any, contentType: CONTENT_TYPE) {
    let domElement: any = null; 
    
    switch (contentType) {
        case CONTENT_TYPE.VIDEO:
            domElement = document.createElement('div') as HTMLElement;
            domElement.style['width'] = width + 'px';
            domElement.style['height'] = height + 'px';
            domElement.style['background'] = '#000';
            domElement.innerHTML = '<iframe id="mIframe_content" src=\"' + info.url 
                + '\" width=\"' + width + '\" height=\"' + height 
                + '\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>';
        break;

        case CONTENT_TYPE.IMAGE:
            domElement = document.createElement('img') as HTMLImageElement;
            domElement.src = getImageAssetURL(imageAssets, info.url);
            domElement.width = width;
        break;

        case CONTENT_TYPE.TEXT:
            domElement = document.createElement('p') as HTMLElement;
            domElement.innerHTML = info.p;
        break;

        default:
        break;
    }

    return domElement;
}

const buildContentNode = function(width: number, height: number, url: string, contentType: CONTENT_TYPE) {
    const content = document.createElement('div') as HTMLElement;
    content.id = '_content';

    if(contentType === CONTENT_TYPE.IMAGE) {
        // Image content
        content.innerHTML = '<img src=\"avseoul/assets/' + getImageAssetURL(imageAssets, url) + '\" width=\"' 
                + width + '\" height=\"' + height + '\">';

    } else {
        // Video and realtime content
        content.style.cursor = 'pointer';
        content.style.width = width.toString() + 'px';
        content.style.height = height.toString() + 'px';
        content.style.background = '#000';
        
        window.setTimeout(() => {
            content.style.opacity = '1';
            content.innerHTML = '<iframe src=\"' + url + '\" width=\"' + width 
                + '\" height=\"' + height 
                + '\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>';
        }, 500);
    }

    return content;
}

const buildContentContainerNode = function (this: any, contentData: any, nodeIndex: number) {
    const container = document.createElement('div') as HTMLElement;
    
    const thumbnailMask = document.createElement('div') as HTMLElement;
    const thumbnail = new Image() as HTMLImageElement;
    
    const descriptionMask = document.createElement('div') as HTMLElement;
    const subject = document.createElement('div') as HTMLElement;
    const description = document.createElement('div') as HTMLElement;
    const date = document.createElement('div') as HTMLElement;
    const detail = document.createElement('rainbow_p') as HTMLElement;

    // Node tree
    container.appendChild(thumbnailMask);
    container.appendChild(descriptionMask);

    thumbnailMask.appendChild(thumbnail);

    descriptionMask.appendChild(subject);
    descriptionMask.appendChild(description);
    descriptionMask.appendChild(date);
    descriptionMask.appendChild(detail);

    // Nodes attributes
    container.className = 'ui_container';
    container.id = contentData.id;

    thumbnailMask.className = 'ui_thumbnail_mask';
    thumbnail.className = 'ui_thumbnail';
    thumbnail.onclick = expandContent.bind(this, nodeIndex);
    thumbnail.src = getImageAssetURL(imageAssets, contentData.thumbnail_src);

    descriptionMask.className = 'ui_description_mask';
    
    subject.className = 'ui_subject';
    subject.onclick = expandContent.bind(this, nodeIndex);
    subject.style.cursor = 'pointer';
    subject.innerHTML = '<av_title>' + contentData.title + '</av_title>';

    description.className = 'ui_description';
    description.innerHTML = '<p>' + contentData.description + '</p>';

    date.className = 'ui_date';
    date.innerHTML = '<p>' + contentData.date + '</p>';

    detail.className = 'ui_detail';
    detail.onclick = expandContent.bind(this, nodeIndex);
    detail.innerHTML = '<a>details</a>'
    
    return container;
}

const init = function (this: any) {
    shuffleRandomScreeningContent();

    const container = document.getElementById('container') as HTMLElement;
    for (let i = 0; i < contentJSON['projects'].length; i++) {
        const s = contentJSON['projects'][i];
        const scriptNode = buildContentContainerNode(s, i);
        container?.append(scriptNode);
    }

    // Assign onclick events
    const rainbowNode = document.getElementById('rainbow') as HTMLElement;
    rainbowNode.onclick = renderRainbow.bind(this);
    const linksAliveNode = document.getElementById('linksAlive') as HTMLElement;
    linksAliveNode.onclick = linksAlive.bind(this);
    const shuffleRandomContent = document.getElementById('shuffleRandomContent') as HTMLElement;
    shuffleRandomContent.addEventListener("click", shuffleRandomScreeningContent.bind(this), {passive: true});

    if (isMobile()) setMobileCSS();
}

document.addEventListener('DOMContentLoaded', init, false);
window.addEventListener('hashchange', locateToHash);